<template>
  <div>
    <div class="link_header lightest-bg">收货地址</div>
    <div class="address">
      <div class="addAddress assistant-text">新增收货地址</div>
      <div class="address_id">
        <span class="dark-text">当前配送至</span>
        <span class="dark-text">中国大陆</span>
      </div>
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="地址信息" prop="add">
          <el-cascader
            size="large"
            :options="options"
            v-model="ruleForm.add"
            @change="addressChange"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="详细地址" prop="address">
          <el-col :span="11">
            <el-input
              type="textarea"
              v-model="ruleForm.address"
              :autosize="{ minRows: 2, maxRows: 3}"
            ></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="邮政编码" prop="zip">
          <el-col :span="11">
            <el-input v-model="ruleForm.zip"></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="收货人姓名" prop="name">
          <el-col :span="11">
            <el-input v-model="ruleForm.name"></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="手机号" required>
          <el-col :span="4">
            <el-form-item prop>
              <el-select v-model="ruleForm.iphone" placeholder="中国大陆">
                <el-option label="中国大陆" value="86"></el-option>
                <el-option label="中国香港" value="852"></el-option>
                <el-option label="中国澳门" value="853"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item prop="phone">
              <el-input v-model="ruleForm.phone" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item>
          <el-checkbox v-model="checked">设置为默认收货地址</el-checkbox>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
        </el-form-item>
      </el-form>
      <div class="table">
        <el-table :data="tableData" style="width: 100%" border>
          <el-table-column prop="name" label="收货人" width="100"></el-table-column>
          <el-table-column prop="province" label="所在地区" width="180"></el-table-column>
          <el-table-column prop="address" label="详细地址" width="180"></el-table-column>
          <el-table-column prop="zip" label="邮编" width="100"></el-table-column>
          <el-table-column prop="phone" label="电话/手机" width="150"></el-table-column>
          <el-table-column label="操作" width="180">
            <template slot-scope="scope">
              <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
              <el-button
                size="mini"
                type="danger"
                @click.native.prevent="deleteaddress(scope.$index, scope.row)"
              >删除</el-button>
            </template>
          </el-table-column>
          <el-table-column label="操作" width="88">
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="handclick(scope.$index, scope.row)"
                type="text"
                size="small"
                :class="scope.row.default==1?'active':isActive"
              >设为默认</el-button>
              <!-- <span @click.native.prevent="deleteRow(scope.$index, tableData)">设为默认</span> -->
            </template>
          </el-table-column>
        </el-table>
      </div>
      <el-dialog title="修改收货地址" :visible.sync="dialogFormVisible">
        <el-form
          :model="saveForm"
          :rules="rules"
          ref="saveForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="地址信息" prop="add">
            <el-cascader
              size="large"
              :options="options"
              v-model="saveForm.add"
              @change="addressChange"
            ></el-cascader>
          </el-form-item>
          <el-form-item label="详细地址" prop="address">
            <el-col :span="12">
              <el-input
                type="textarea"
                v-model="saveForm.address"
                :autosize="{ minRows: 2, maxRows: 3}"
              ></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="邮政编码" prop="zip">
            <el-col :span="12">
              <el-input v-model="saveForm.zip"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="收货人姓名" prop="name">
            <el-col :span="12">
              <el-input v-model="saveForm.name"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="手机号" required>
            <el-col :span="5">
              <el-form-item prop>
                <el-select v-model="saveForm.iphone" placeholder="中国大陆">
                  <el-option label="中国大陆" value="86"></el-option>
                  <el-option label="中国香港" value="852"></el-option>
                  <el-option label="中国澳门" value="853"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="7">
              <el-form-item prop="phone">
                <el-input v-model="saveForm.phone" autocomplete="off"></el-input>
              </el-form-item>
            </el-col>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="submitclick">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import { regionData, CodeToText, TextToCode } from "element-china-area-data";
export default {
  data() {
    var checkphone = (rule, value, callback) => {
      // let phoneReg = /(^1[3|4|5|6|7|8|9]\d{9}$)|(^09\d{8}$)/;
      if (value == "") {
        callback(new Error("请输入手机号"));
      } else if (!this.isCellPhone(value)) {
        //引入methods中封装的检查手机格式的方法
        callback(new Error("请输入正确的手机号!"));
      } else {
        callback();
      }
    };
    var checkzip = (rule, value, callback) => {
      if (value == "") {
        callback(new Error("请输入邮政编码"));
      } else if (!this.iscellZip(value)) {
        callback(new Error("请输入正确的邮政编码"));
      } else {
        callback();
      }
    };
    return {
      isShow: false,
      options: regionData,
      checked: false,
      isActive: false,
      dialogFormVisible: false,
      id: "",
      default: "",
      ruleForm: {
        address: "",
        province: "",
        city: "",
        district: "",
        add: "",
        name: "",
        place: "",
        phone: "",
        zip: ""
      },
      saveForm: {
        address: "",
        province: "",
        city: "",
        district: "",
        add: "",
        name: "",
        place: "",
        phone: "",
        zip: ""
      },

      rules: {
        add: [{ required: true, message: "请输入收货地址", trigger: "blur" }],
        address: [
          { required: true, message: "请输入详细地址", trigger: "blur" }
        ],
        name: [
          { required: true, message: "请输入收货人姓名", trigger: "blur" }
        ],
        phone: [
          {
            required: true,
            validator: checkphone,
            trigger: "blur"
          } //validator: checkphone是表单验证
          // { type: "number", message: "手机必须为数字值" }
        ],
        zip: [
          {
            required: true,
            validator: checkzip,
            trigger: "blur"
          }
        ]
      },
      tableData: []
    };
  },
  mounted() {
    this.addressList();
  },
  methods: {
    // 验证手机正则表达试
    isCellPhone(val) {
      if (!/^1(3|4|5|6|7|8)\d{9}$/.test(val)) {
        return false;
      } else {
        return true;
      }
    },
    iscellZip(val) {
      if (!/^[0-9]{6}$/.test(val)) {
        return false;
      } else {
        return true;
      }
    },
    addressChange(arr) {
      this.province = CodeToText[arr[0]];
      this.city = CodeToText[arr[1]];
      this.district = CodeToText[arr[2]];
    },
    // 立即创建
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.checked == false) {
            this.default = 0;
          } else {
            this.default = 1;
          }
          this.saveaddress();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    // 编辑按钮
    handleEdit(index, row) {
      this.dialogFormVisible = true;
      this.saveForm = row;
      this.saveForm.add = TextToCode[row.province][row.city][row.district].code;
      this.id = row.id;
    },
    // 修改提交
    submitclick() {
      this.dialogFormVisible = false;
      var _this = this;
      var url = this.APIURL + "/classic/usr/address/save";
      var data = {
        name: this.saveForm.name,
        phone: this.saveForm.phone,
        province: this.saveForm.province,
        city: this.saveForm.city,
        district: this.saveForm.district,
        address: this.saveForm.address,
        default: this.default,
        id: this.id
      };
      this.$axios
        .post(url, data, { headers: { SessionKey: this.SessionKey,key:this.key } })
        .then(res => {
          if (res.data.errorCode == 0) {
            this.$message({
              type: "success",
              message: "修改成功!"
            });
            this.addressList();
          } else {
            alert(res.data.msg);
          }
        })
        .catch(err => {});
    },
    // 地址列表
    addressList() {
      var _this = this;
      var url = this.APIURL + "/classic/usr/address/all";
      this.$axios
        .post(url, "", { headers: { SessionKey: this.SessionKey,key:this.key } })
        .then(res => {
          // console.log()
          _this.tableData = res.data.data;
        })
        .catch(err => {});
    },
    // 新增收货地址
    saveaddress() {
      var _this = this;
      var url = this.APIURL + "/classic/usr/address/save";
      var data = {
        name: this.ruleForm.name,
        phone: this.ruleForm.phone,
        province: this.province,
        city: this.city,
        district: this.district,
        address: this.ruleForm.address,
        default: this.default
      };
      this.$axios
        .post(url, data, { headers: { SessionKey: this.SessionKey,key:this.key }})
        .then(res => {
          if (res.data.errorCode == 0) {
            this.addressList();
          } else {
            alert(res.data.msg);
          }
        })
        .catch(err => {});
    },
    // 删除地址列表
    deleteaddress(index, rows) {
      this.$confirm("此操作将永久删除, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          var _this = this;
          var url = this.APIURL + "/classic/usr/address/delete";
          this.$axios
            .post(
              url,
              { id: rows.id },
              { headers: { SessionKey: this.SessionKey,key:this.key } }
            )
            .then(res => {
              if (res.data.errorCode == 0) {
                this.$message({
                  type: "success",
                  message: "删除成功!"
                });
                this.addressList();
              } else {
                alert(res.data.msg);
              }
            })
            .catch(err => {});
        })
        .catch(() => {});
    },
    // 设为默认地址
    handclick(index, rows) {
      var _this = this;
      var url = this.APIURL + "/classic/usr/address/save";
      var data = {
        name: rows.name,
        phone: rows.phone,
        province: rows.province,
        city: rows.city,
        district: rows.district,
        address: rows.address,
        default: 1,
        id: rows.id
      };
      this.$axios
        .post(url, data, { headers: { SessionKey: this.SessionKey,key:this.key } })
        .then(res => {
          if (res.data.errorCode == 0) {
            this.addressList();
          } else {
            alert(res.data.msg);
          }
        })
        .catch(err => {});
    }
    //
  },
  computed: {
    SessionKey() {
      return this.$store.state.SessionKey;
    },
    key() {
      return this.$store.state.key;
    },
  },
  components: {}
};
</script>
<style lang="scss">
.address {
  width: 100%;
  height: 1122px;
  background: rgba(255, 255, 255, 0.6);
  .addAddress {
    font-size: 14px;
    font-family: PingFang SC Regular;
    margin: 10px 0 0 18px;
  }
  .address_id {
    width: 540px;
    height: 24px;
    background: rgba(196, 190, 179, 0.3);
    margin: 18px 0 16px 28px;
    span {
      font-size: 12px;
      font-family: PingFang SC Regular;
      margin: 0 36px 0 10px;
    }
  }
  .el-table th.is-leaf,
  .el-table td {
    text-align: center;
  }
  .active {
    background: #c3beb4;
    color: #fff;
  }
}
</style>
